* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

body {
  min-width: 320px;
  height: 100vh;
  padding-top: 15px;
  font-family: 'Work Sans', sans-serif;
  font-weight: 800;
  background-image: url('./../img/bg560m.jpg');
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;

  @media (min-width: 560px) {
    background-image: url('./../img/bg.png');
    background-position: center center;
    background-size: cover;
  }
}

.Wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  overflow: hidden;
}

.Title {
  margin-bottom: 10px;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: 768px) {
    height: 25vh;
    margin-bottom: 10px;
  }

  &-Image {
    position: relative;
    display: inline-block;
    width: 220px;
    margin-bottom: -10px;
    z-index: 3;

    @media (min-width: 768px) {
      width: 250px;
    }

    @media (min-width: 1600px) {
      width: 290px;
    }
  }

  &-Text {
    padding: 10px 25px;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    border-radius: 8px;

    @media (min-width: 768px) {
      padding: 10px 40px;
    }
  }

  &-Spin {
    position: relative;
    font-size: 25px;
    color: #fff;
    background-color: #de2b00;
    transform: rotate(-1deg);
    z-index: 2;

    @media (min-width: 768px) {
      font-size: 30px;
    }

    @media (min-width: 1600px) {
      font-size: 33px;
    }
  }

  &-Win {
    position: relative;
    font-size: 22px;
    color: #000;
    background-color: #fff;
    transform: rotate(1deg);
    z-index: 1;

    @media (min-width: 768px) {
      font-size: 27px;
    }

    @media (min-width: 1600px) {
      font-size: 30px;
    }
  }
}

.Spin {
  position: relative;

  @media (min-width: 768px) {
    height: 75vh;
  }

  &-Spinner {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    width: 20%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    transition: filter 0.2s ease;

    &--active {
      filter: drop-shadow(10px 0 4px rgba(0,0,0,0.7));
    }
  }

  &-Circle {
    position: relative;
    z-index: 1;
    display: block;
    transition: transform 6s cubic-bezier(0.3, 1, 0.7, 1);
    will-change: transform;

    @media (min-width: 768px) {
      height: 100%;
    }
  }

  &-Fire {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: none;
    width: 130%;
    transform: translate(-50%, -50%);
    mix-blend-mode: screen;
  }
}

.Modal {
  position: fixed;
  top: 0;
  z-index: 1000;
  //display: none;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0,0,0,0.75);
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 0;
  }

  &-Content {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 250px;
    background-color: #fff;
    background-image: url('./../img/pattern-min-m.jpg');
    background-size: 102% 45%;
    background-repeat: no-repeat;
    background-position: 0 bottom;
    border-radius: 12px;
    transform: translate(-50%, -50%);

    @media (min-width: 560px) {
      width: 350px;
    }
  }

  &-Image {
    position: absolute;
    z-index: 1;
    margin-top: -14%;
    left: -20%;
    right: -20%;
  }

  &-Text {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 150px / 320px * 100%;
    text-transform: uppercase;

    div {
      display: inline-block;
      border-radius: 8px;
      white-space: nowrap;
    }
  }

  &-Won, &-Bet {
    padding: 7px 25px;
    font-size: 20px;
    color: #000;
    background-color: #ffcc00;

    @media (min-width: 560px) {
      font-size: 26px;
      padding: 10px 25px;
    }
  }

  &-Won {
    margin-bottom: 10px;
    transform: rotate(-1.7deg);
  }

  &-Prize {
    position: relative;
    z-index: 1;
    margin-bottom: 5px;
    padding: 7px 55px;
    font-size: 30px;
    color: #fff;
    background-color: #de2b00;
    transform: rotate(-3deg);
    text-shadow: 3px 3px 3px rgba(0,0,0,0.25);

    @media (min-width: 560px) {
      font-size: 42px;
      padding: 10px 100px;
    }
  }

  &-Bet {
    margin-bottom: 5%;
    transform: rotate(3deg);
  }

  &-Arrow {
    display: block;
    width: 10%;
    margin: 0 auto 5%;
  }

  &-Link {
    display: inline-block;
    margin-bottom: 12%;
    padding: 10px 20px;
    color: #000;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;
    background: linear-gradient(to right, #7cea29, #10c713);
    border-radius: 2px;
    text-shadow: 2px 2px 1px rgba(0,0,0,0.25);
    box-shadow: 5px 5px 5px rgba(0,0,0,0.25);
    cursor: pointer;
    white-space: nowrap;

    @media (min-width: 560px) {
      font-size: 26px;
    }

    &:hover {
      background: linear-gradient(to right, #fc664d, #fd3b1f);
    }
  }
}
